<template>
  <div>
    <div class="crumbs">
      <el-breadcrumb separator="/">
        <el-breadcrumb-item>
          <i class="el-icon-lx-cascades"></i> 
        </el-breadcrumb-item>
      </el-breadcrumb>
    </div>
    <div class="container">
      <div class="handle-box">
        <el-input v-model="query.college_name" placeholder="" class="handle-input mr10"></el-input>
        <el-input v-model="query.major_name" placeholder="" class="handle-input mr10"></el-input>
        <el-button type="primary" icon="el-icon-search" @click="handleSearch"></el-button>
        <el-button type="primary" icon="el-icon-lx-roundclose" @click="handleReset"></el-button>
      </div>

      <div class="handle-box">
        <el-button
            v-if="userState==='20'"
            type="primary"
            icon="el-icon-lx-searchlist"
            class="handle-del mr10"
            @click="countNums"
        ></el-button>

        <el-button
            v-if="optionSwitch"
            type="primary"
            icon="el-icon-lx-friendadd"
            class="handle-del mr10"
            @click="handleAdd"
        ></el-button>

        <div class="float"  v-if="userState==='20'">
          <div class="el-upload__tip" slot="tip">
            
            <el-switch
                v-model="optionSwitch">
            </el-switch>
          </div>
        </div>

      </div>

      <!--  -->
      <el-dialog title="" :visible.sync="editVisible" width="30%">
        <el-form ref="form" :model="editParam" label-width="70px">
          <el-form-item label="ID">
            <el-input type="number" v-model="editParam.major_id" class="handle-input2 mr10" :disabled="true"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="editParam.major_name" placeholder="" class="handle-input2 mr10"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="editParam.college_name" placeholder="" class="handle-input2 mr10" :disabled="true"></el-input>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
                <el-button @click="editVisible = false"> </el-button>
                <el-button type="primary" @click="saveEdit"> </el-button>
            </span>
      </el-dialog>

      <!--  -->
      <el-dialog title="" :visible.sync="addVisible" width="30%">
        <el-form ref="form" :model="addParam" label-width="70px">
          <el-form-item label="">
            <el-input v-model="addParam.major_name" placeholder="" class="handle-input2 mr10"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="addParam.college_name" placeholder="" class="handle-input2 mr10"></el-input>
          </el-form-item>
        </el-form>

        <span slot="footer" class="dialog-footer">
                <el-button @click="addVisible = false"> </el-button>
                <el-button type="primary" @click="addMajorSubmit"> </el-button>
            </span>
      </el-dialog>

      <el-table
          :data="tableData"
          border
          class="table"
          ref="multipleTable"
          header-cell-class-name="table-header"
      >

        <el-table-column prop="majorId" label="ID" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="collegeId" label="" min-width="20%" align="center"></el-table-column>
        <el-table-column prop="majorName" label="" min-width="20%" align="center"></el-table-column>
        <el-table-column prop="courseNum" label="" min-width="10%" align="center"></el-table-column>
        <el-table-column prop="requireNum" label="" min-width="10%" align="center" v-if="false"></el-table-column>
        <el-table-column label="" min-width="10%" align="center">
          <template slot-scope="scope">
            <el-button
                type="text"
                @click="handleTofix(scope.row.majorName)"
            >{{scope.row.requireNum}}</el-button>
          </template>
        </el-table-column>
        <el-table-column label="" min-width="12%" align="center" fixed="right"
                         v-if="optionSwitch">
          <template slot-scope="scope">
            <el-button
                v-if="userState==='20'"
                type="text"
                icon="el-icon-edit"
                @click="handleEdit(scope.$index, scope.row)"
            >
            </el-button>
            <el-button
                v-if="userState==='20'"
                type="text"
                icon="el-icon-delete"
                class="red"
                @click="handleDelete(scope.$index, scope.row)"
            >
            </el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="pagination">
        <el-pagination
            background
            layout="total, prev, pager, next"
            :current-page="query.page"
            :page-size="query.size"
            :total="pageTotal"
            @current-change="handlePageChange"
        ></el-pagination>
      </div>
    </div>

  </div>
</template>

<script>
import {
  addMajor,
  countMajorNums,
  deleteMajor,
  getMajor,
  getPersonalMessage,
  updateMajor
} from '../../api/index';
import bus from "@/components/common/bus";

export default {
  name: 'major',
  data() {
    return {
      userState: "",
      myCollege:"",
      optionSwitch:false,
      query: {
        page: 1,
        size: 10,
        major_id: "",
        major_name: "",
        college_name: ""
      },
      editParam:{
        major_id: "",
        major_name: "",
        college_name:""
      },
      addParam:{
        major_name: "",
        college_name:""
      },
      tableData: [],
      multipleSelection: [],
      delList: [],
      editVisible: false,
      addVisible: false,
      pageTotal: 0,
      form: {},
      idx: -1,
      id: -1
    };
  },
  created() {
    this.userState = "";
    this.myCollege = "";
    this.getData();
    getPersonalMessage().then(res => {
      if (res.status === 200) {
        this.userReturn=res.data.data;
        this.userState=this.userReturn.userState;
        this.myCollege=this.userReturn.college;
      }
    });
  },
  methods: {

    getData() {
      getMajor(this.query).then(
          res => {
            this.resultObj = res.data;
            this.tableData = this.resultObj.data;
            this.pageTotal = this.resultObj.total;
          }
      );
    },
    handleReset() {
      this.query.page = 1;
      this.query.size = 10;
      this.query.major_id = "";
      this.query.college_name = "";
      this.query.major_name = "";
      this.getData();
    },
    // 
    handleSearch() {
      this.$set(this.query, 'page', 1);
      this.getData();
    },
    // 
    handlePageChange(val) {
      this.$set(this.query, 'page', val);
      this.getData();
    },
    //
    countNums(){
      countMajorNums().then(res => {
        this.$message.success('');
      });
    },
    handleTofix(majorName) {
      bus.$emit("checkMajorRequire",majorName)
      this.$router.push({ path: '/course', query: { majorName: majorName }});
    },
    //
    handleEdit(index, row){
      this.editParam.major_id=this.tableData[index].majorId;
      this.editParam.major_name=this.tableData[index].majorName;
      this.editParam.college_name=this.tableData[index].collegeId;
      this.editVisible=true;
    },
    //
    saveEdit() {
      this.editVisible = false;
      updateMajor(this.editParam.major_id,this.editParam.major_name).then(
          res => {
            if(res.data.code===-1){
              this.$message.error('');
            }else{
              this.$message.success("");
              this.getData();
            }
          }, error => {
            this.$message.error("");
          }
      );
    },
    // 
    handleDelete(index, row) {
      // 
      this.$confirm('', '', {
        type: 'warning'
      })
          .then(() => {
            deleteMajor(this.tableData[index].majorId).then(
                res => {
                  if(res.data.code===-1){
                    this.$message.error(',');
                  }else{
                    this.$message.success('');
                    this.handleSearch();
                  }
                }, error => {
                  this.$message.error('');
                }
            )
          })
          .catch(() => {
          });
    },
    handleAdd(){
      this.addVisible=true;
    },
    //
    addMajorSubmit(){
      if (this.addParam.major_name === ""||this.addParam.college_name === "") {
        this.$message.info("");
        return;
      }
      this.addVisible = false;
      addMajor(this.addParam.major_name,this.addParam.college_name).then(
          res => {
            if(res.data.code===-1){
              this.$message.error('');
            }else{
              this.$message.success("");
              this.getData();
            }
          }, error => {
            this.$message.error("");
          }
      );
    }
  }
};
</script>

<style scoped>
.handle-box {
  margin-bottom: 20px;
}

.handle-select {
  width: 120px;
}

.handle-input {
  width: 300px;
  display: inline-block;
}

.table {
  width: 100%;
  font-size: 14px;
}

.red {
  color: #ff0000;
}

.mr10 {
  margin-right: 10px;
}

.float {
  float: right;
}
.mt10{
  margin-top: 10px;
}

.table-td-thumb {
  display: block;
  margin: auto;
  width: 40px;
  height: 40px;
}
</style>
